.default {

}

.red {
    background: red;
}

.button {
    font-weight: 700;
    color: white;
    text-decoration: none;
    padding: 2vh 10vh;
    border-radius: 2px;
    background: rgb(64, 199, 129);
    box-shadow: 0 -3px rgb(53, 167, 110) inset;
    transition: 0.2s;
}

.button:hover {
    background: rgb(53, 167, 110);
}

.button:active {
    background: rgb(33, 147, 90);
    box-shadow: 0 3px rgb(33, 147, 90) inset;
}